@import 'https://fonts.googleapis.com/css?family=Open+Sans:400,400i,600,600i';
.Products, .AdminNav-container, .Admin-container, .PackagingList, .ProductList {
  max-width: 1280px;
  margin: 0 auto;
  padding: 0 40px;
  list-style: none;
}

@media (max-width: 639px) {
  .Products, .AdminNav-container, .Admin-container, .PackagingList, .ProductList {
    padding: 0 15px;
  }
}

.ProductAdded,
.ProductDetail, .Cart, .Orders, .OrderDetail {
  width: 100%;
  max-width: 760px;
  margin: 0 auto;
  padding: 40px;
  border-radius: 7px;
  background-color: #fff;
}

@media (max-width: 639px) {
  .ProductAdded,
  .ProductDetail, .Cart, .Orders, .OrderDetail {
    padding: 20px;
    border-radius: 0;
  }
}

@media (min-width: 640px) {
  .ProductAdded,
  .ProductDetail, .Cart, .Orders, .OrderDetail {
    box-shadow: 0 2px 2px rgba(0, 0, 0, 0.2);
  }
}

*,
*:after,
*:before {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: 'Open Sans', sans-serif;
  font-size: 16px;
  background-color: #f7f7f7;
  overflow: scroll;
}

body.main {
  padding: 0 0 100px;
}

@media (max-width: 639px) {
  body.main {
    background-color: #fff;
    padding-top: 130px;
  }
}

@media (min-width: 640px) {
  body.main {
    padding: 160px 40px 100px;
  }
  body.main.products_page, body.main.product_page {
    background-color: #333;
  }
  body.main.checkout_page {
    background-color: #ec652a;
  }
}

.Product {
  display: flex;
  flex-direction: column;
  width: 100%;
  padding: 10px 10px 20px;
  background-color: #fff;
  border-radius: 5px;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.1);
  color: inherit;
}

.Product, .Product:hover {
  text-decoration: none;
}

.Product-image {
  padding-top: 70%;
  background: #eaeaea 50% 50%;
  background-size: cover;
  border-radius: 2px;
}

.Product-info {
  text-align: center;
}

.Product-name {
  margin: 20px 0 10px;
  font-size: 15px;
  font-weight: 600;
}

.Product-description {
  margin: 0;
  padding: 0 10px;
  font-size: 13px;
  color: #444;
}

.Products-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  padding: 0;
  margin: 0 0 0 -30px;
  list-style: none;
}

.Products-item {
  display: flex;
  width: 100%;
  max-width: 300px;
  padding: 0 0 30px 30px;
}

.ProductAdded,
.ProductDetail {
  max-width: 690px;
}

.ProductDetail-header {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
  padding-bottom: 20px;
  border-bottom: 1px solid #ccc;
}

.ProductAdded-title {
  flex-grow: 1;
  color: #666;
}

.ProductDetail-imageWrap {
  flex-shrink: 0;
  width: 15%;
}

.ProductDetail-image {
  padding-top: 100%;
  background-size: cover;
  background-color: #eaeaea;
  border-radius: 5px;
}

.ProductDetail-info {
  flex-grow: 1;
  padding: 10px 20px;
}

.ProductDetail-name {
  margin: 0;
  font-size: 17px;
  font-weight: 600;
}

.ProductDetail-description {
  margin: 0;
  font-size: 14px;
  color: #444;
}

.ProductDetail-optionGroup {
  margin-bottom: 20px;
}

.ProductDetail-optionLabel {
  margin-bottom: 10px;
  display: inline-block;
  font-size: 13px;
  text-transform: uppercase;
  font-weight: 600;
  color: #616161;
}

.Cart-title > p {
  margin-top: 0;
}

.Cart-list {
  margin-bottom: 40px;
  padding: 0;
  border-top: 1px solid #e6e6e6;
  list-style: none;
}

.Cart-item {
  padding: 15px 0;
  border-bottom: 1px solid #e6e6e6;
}

@media (max-width: 639px) {
  .Cart-empty {
    padding-top: 20px;
  }
}

.CartItem {
  display: flex;
  margin-left: -10px;
  flex-direction: row;
  align-items: center;
}

.CartItem > div {
  padding-left: 10px;
}

.CartItem-qty {
  min-width: 80px;
}

.CartItem-imageWrap {
  width: 25%;
  max-width: 90px;
}

.CartItem-info {
  flex-grow: 1;
}

.CartItem-name {
  margin: 0;
  font-size: 15px;
  font-weight: 600;
}

.CartItem-name, .CartItem-name:visited, .CartItem-name:hover {
  text-decoration: none;
  color: #08c;
}

.CartItem-description {
  margin: 0;
  font-size: 13px;
  color: #808080;
}

.CartItem-image {
  padding-top: 100%;
  background-size: cover;
  background-color: #eaeaea;
  border-radius: 5px;
}

.CartItem-removeLink {
  font-size: 14px;
  text-decoration: underline;
  cursor: pointer;
}

.CartItem-removeLink, .CartItem-removeLink:hover {
  color: #cc001d;
}

@media (max-width: 639px) {
  .CartItem {
    flex-wrap: wrap;
  }
  .CartItem-imageWrap {
    flex-shrink: 0;
    min-width: 0;
  }
  .CartItem-info {
    flex-shrink: 0;
    width: 75%;
    padding-left: 20px;
  }
  .CartItem-qty {
    margin-top: 10px;
  }
  .CartItem-remove,
  .CartItem-qty {
    width: 50%;
  }
  .CartItem-qty .Form-control {
    height: 30px;
    font-size: 14px;
  }
  .CartItem-remove {
    text-align: right;
  }
  .CartItem-removeLink {
    display: inline-block;
    padding: 7px;
  }
}

@media (min-width: 640px) {
  .CartItem-remove {
    padding-left: 20px;
    white-space: nowrap;
  }
}

.NavBar {
  position: fixed;
  z-index: 1;
  top: 0;
  left: 0;
  width: 100%;
  background-color: #ec652a;
  color: #fff;
}

.NavBar-container {
  display: flex;
  align-items: center;
  height: 80px;
  max-width: 1180px;
  margin: 0 auto;
  padding: 10px 20px;
}

.NavBar-logo {
  flex-grow: 1;
  text-align: center;
}

.NavBar-logoImage {
  height: 65px;
}

.NavBar-back {
  flex-shrink: 0;
  width: 110px;
}

.NavBar-account {
  flex-shrink: 0;
  width: 110px;
  text-align: right;
}

.NavBar-link {
  position: relative;
  display: inline-block;
  color: #fff;
  text-decoration: none;
}

.NavBar-link, .NavBar-link:hover {
  color: #fff;
}

.NavBar-link--account {
  margin-right: 8px;
}

.Navbar-cartQty {
  position: absolute;
  z-index: 2;
  top: -8px;
  right: -8px;
  display: block;
  box-sizing: content-box;
  width: 20px;
  height: 15px;
  border: 1px solid #8d2dea;
  line-height: 15px;
  font-size: 12px;
  border-radius: 8px;
  background: #ec652a;
  text-align: center;
  overflow: hidden;
}

.NavBar-icon {
  height: 17px;
  width: 20px;
  margin-right: 4px;
  margin-top: -4px;
  vertical-align: middle;
  fill: #fff;
}

@media (max-width: 420px) {
  .NavBar-back,
  .NavBar-account {
    width: 50%;
  }
  .NavBar-logo {
    position: absolute;
    top: 40px;
    left: 50%;
    width: 106px;
    margin-left: -53px;
  }
  .NavBar-logoImage {
    height: 55px;
  }
}

.ProductAdded {
  position: relative;
}

.CloseButton {
  position: absolute;
  top: -10px;
  right: -10px;
  cursor: pointer;
}

@media (max-width: 639px) {
  .CloseButton {
    top: 10px;
    right: 10px;
  }
}

.CloseButton-image {
  height: 40px;
  width: 40px;
}

.CheckoutForm-wrap {
  padding: 5px;
  border-radius: 6px;
  background-color: #e3e3e3;
}

.Form-group {
  margin-bottom: 20px;
}

.Form-label {
  display: block;
  margin-bottom: .5rem;
  font-weight: 700;
}

.Form-control {
  height: 46px;
  width: 100%;
  padding: 13px 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: 17px;
  background-color: #fff;
  color: #555;
}

textarea.Form-control {
  max-height: 160px;
  min-height: 38px;
}

.Form-horizontal, .CheckoutForm {
  display: flex;
  margin-left: -8px;
}

.Form-horizontal > .Form-group, .CheckoutForm > .Form-group {
  margin-bottom: 0;
  padding-left: 8px;
}

.Form-inputList {
  list-style: none;
}

.Form-inputList > li {
  display: flex;
  align-items: center;
  margin-top: 10px;
}

.Form-inputList > li:first-child {
  margin-top: 0;
}

.Form-inputList > li > .Form-deleteItem {
  width: 20px;
  margin-left: 10px;
  text-align: right;
  flex-shrink: 1;
}

.Form-inputList--qty {
  max-width: 180px;
}

.Form-inputList--qty .Form-control {
  height: 32px;
  font-size: 15px;
}

.Form-deleteIcon {
  width: 18px;
  height: 18px;
  fill: #7d7d7d;
  cursor: pointer;
}

.Form-deleteIcon:hover {
  fill: #333;
}

.CheckoutForm--email {
  flex-grow: 1;
}

@media (max-width: 639px) {
  .CheckoutForm {
    flex-direction: column;
    margin-top: -5px;
  }
  .CheckoutForm .Form-group {
    margin-top: 5px;
  }
}

.Orders {
  max-width: 960px;
}

.Orders-list {
  width: 100%;
  border-top: 1px solid #ccc;
  border-spacing: 0;
  border-collapse: collapse;
  font-size: 14px;
}

.Orders-list th {
  text-align: left;
}

.Orders-list th,
.Orders-list td {
  padding: 13px 8px;
  border-bottom: 1px solid #ccc;
}

.Orders-date {
  white-space: nowrap;
}

.Orders-description {
  color: #555;
}

@media (max-width: 549px) {
  .Orders-description--desktop {
    display: none;
  }
}

@media (min-width: 550px) {
  .Orders-description--mobile {
    display: none;
  }
}

.OrderDetail {
  max-width: 960px;
}

.Section-heading {
  margin: 0 0 20px;
  text-transform: uppercase;
  color: #333;
  font-weight: 600;
  font-size: 28px;
}

.Section-header {
  margin-bottom: 20px;
}

.Section-header > .Section-heading {
  margin-bottom: 0;
}

.Section-subtitle {
  margin: 0;
}

.nowrap {
  white-space: nowrap;
}

.Admin-link {
  margin: 80px auto;
  width: 100px;
  text-align: center;
  background-color: #8d2dea;
}

.Admin-link > a {
  display: block;
  width: 100%;
  padding: 10px;
}

.Admin-link > a, .Admin-link > a:hover {
  color: #fff;
  text-decoration: none;
}

.AdminNav {
  background-color: #ec652a;
}

.AdminNav a {
  display: inline-block;
  padding: 10px;
  text-decoration: none;
}

.AdminNav a, .AdminNav a:hover {
  color: #fff;
}

.AdminNav a:hover {
  text-decoration: underline;
}

.Admin-container, .PackagingList, .ProductList {
  max-width: 640px;
  margin: 50px auto;
  padding: 2.5em;
  background-color: #fff;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.2);
}

@media (max-width: 639px) {
  .Admin-container, .PackagingList, .ProductList {
    padding: 1.5em;
  }
}

.Admin-header {
  display: flex;
  align-items: center;
  margin-bottom: 2em;
}

.Admin-heading {
  flex-grow: 1;
  margin: 0;
}

.PackagingList {
  max-width: 1280px;
}

.PackagingList table {
  width: 100%;
  margin-bottom: 7em;
  border-top: 1px solid #e4e4e4;
  border-collapse: collapse;
}

.PackagingList th {
  text-align: left;
}

.PackagingList td {
  user-select: none;
  cursor: default;
}

.PackagingList th,
.PackagingList td {
  padding: 1em;
  border-bottom: 1px solid #e4e4e4;
}

.PackagingList th:first-child,
.PackagingList td:first-child {
  padding-left: 0;
}

.PackagingList tr.is-filled td {
  opacity: .4;
  text-decoration: line-through;
}

.PackagingList-toggleFilled {
  cursor: pointer;
}

.PackagingList-name {
  font-weight: 600;
}

tr.is-filled > td > .PackagingList-name {
  font-weight: normal;
}

@media (max-width: 760px) {
  .PackagingList-photo {
    display: none;
  }
}

.PackagingList-choices {
  font-size: 14px;
  color: #4e4e4e;
}

.PackagingList-choices:empty {
  display: none;
}

@media (max-width: 570px) {
  .PackagingList-orderedAt {
    display: none;
  }
}

.PackagingList-check {
  width: 40px;
  height: 40px;
  fill: #969696;
}

.ProductList table {
  width: 100%;
  border-top: 1px solid #e4e4e4;
  border-collapse: collapse;
}

.ProductList table td {
  padding: 1em;
  border-bottom: 1px solid #e4e4e4;
}

.ProductList table td:first-child {
  padding-left: 0;
}

.ProductList-image {
  width: 60px;
  height: 60px;
  border-radius: 5px;
  background-size: cover;
  background-color: #eaeaea;
}

.ProductForm {
  max-width: 480px;
}

.ProductForm-image {
  width: 150px;
  height: 150px;
  margin-top: 1em;
  border-radius: 5px;
  background-size: cover;
  background-color: #eaeaea;
}

.OptionsMatrix {
  table-layout: fixed;
  width: 100%;
  margin-bottom: 20px;
  border-collapse: collapse;
}

.OptionsMatrix th,
.OptionsMatrix td {
  padding: 10px;
  border-bottom: 1px solid #ccc;
  border-right: 1px solid #ccc;
  background-color: #fff;
}

.OptionsMatrix th {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  font-size: .75em;
}

.OptionsMatrix thead > tr > th {
  border-top: 1px solid #ccc;
}

.OptionsMatrix thead > tr > th:first-child {
  border-top: none;
  background-color: transparent;
}

.OptionsMatrix tbody > tr > th {
  border-left: 1px solid #ccc;
  text-align: right;
}

.OptionsMatrix tbody > tr > td {
  text-align: center;
}

.Button {
  display: inline-block;
  margin: 0;
  cursor: pointer;
  text-align: center;
  vertical-align: middle;
  white-space: nowrap;
  text-decoration: none;
  border: 0 none;
  border-radius: 5px;
  padding: 15px;
  line-height: 1;
  font-size: 16px;
}

.Button--flush {
  width: 100%;
}

.Button:active, .Button:focus {
  outline: none !important;
}

.Button:disabled, .Button[disabled], .Button.is-disabled {
  cursor: not-allowed;
  pointer-events: none;
  opacity: 0.50;
  box-shadow: none;
  text-shadow: none;
}

.Button--primary, .Button--add-to-cart {
  background-color: #2a8cec;
  color: #fff;
}

.Button--order {
  background-color: #2a8cec;
  color: #fff;
}

.Button--cancel {
  background-color: #2a8cec;
  color: #fff;
}

.OptionList {
  display: flex;
  flex-wrap: wrap;
  margin: 0;
  padding: 0;
  list-style: none;
  font-size: 13px;
}

.OptionList-item {
  margin: 0 10px 10px 0;
  padding: 6px 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
  cursor: pointer;
  user-select: none;
  background-color: #f3f3f3;
  color: #333;
}

.OptionList-item.is-selected {
  border-color: #ec652a;
  background-color: #ec652a;
  color: #fff;
}

.OptionList-item.is-disabled {
  opacity: 0.5;
  cursor: default;
}
